.user {
    visibility: hidden;
    transform: translateY(200%);
    font-size: 3rem;
    text-align: center;
    font-style: italic;
    transition: all 1s;
}
.u-totop {
    transform: none;
    visibility: visible;
}
.head {
    display: flex;
    justify-content: space-around;
    padding: 3rem 1rem;
    background-color: green;
    margin-bottom: 2rem;
    color: white;
}
.full-name {
    font-size: 2.5rem;
    font-weight: 400;
}
.user-bio {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    text-align: center;
    padding: 1rem;
}

.user-bio p {
    font-size: 1.5rem;
    padding: 1rem;
}
.user-bio span {
    font-size: 2rem;
    font-weight: 500;
    margin-right: 1rem;
    color: black;
}
.b-details {
    margin: 3rem 0;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.b-details h2 {
    font-size: 3rem;
    padding: 2rem 0;
}
.month {
    background-image: linear-gradient(to right, rgb(255, 187, 0), rgb(25, 248, 25), rgb(255, 187, 0));
    color: white;
}
.month, .status {
    display: flex;
}
.month p {
    width: 10rem;
    height: 3rem;
    padding-top: 1rem;
    text-align: center;
    border: 1px solid black;
    font-size: 1.5rem;
    font-weight: 700;
}

.status p {
    width: 10rem;
    height: 3rem;
    padding-top: 1rem;
    text-align: center;
    font-size: 1.3rem;
    border: 1px solid black;
}